// pages/shifts.js
import { useState } from "react";
import Layout from "../components/Layout";
import { useShift } from "../context/ShiftContext";

/* SafeDate ÿ®ÿ≥Ÿäÿ∑ ŸÑÿπÿ±ÿ∂ ÿßŸÑÿ™Ÿàÿßÿ±ŸäÿÆ */
function SafeDate({ value }) {
  if (typeof window === "undefined") return "";
  try {
    return new Date(value).toLocaleString("ar-EG");
  } catch {
    return "";
  }
}

export default function ShiftsPage() {
  const [user] = useState({ name: "ÿ£ÿ≠ŸÖÿØ", role: "admin" });
  const {
    shifts,
    currentShift,
    openShift,
    closeCurrentShift,
  } = useShift();

  const [openingNote, setOpeningNote] = useState("");
  const [closingNote, setClosingNote] = useState("");

  const handleOpenShift = () => {
    openShift(user.name, openingNote);
    setOpeningNote("");
  };

  const handleCloseShift = () => {
    closeCurrentShift(closingNote);
    setClosingNote("");
  };

  const formatCurrency = (v) =>
    `${Number(v || 0).toLocaleString("ar-SA")} ÿ±.ÿ≥`;

  const handlePrintShift = (shift) => {
    const t = shift.totals || {};
    const html = `
      <html dir="rtl" lang="ar">
        <head>
          <meta charset="utf-8" />
          <title>ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑÿ¥ŸÅÿ™ ${shift.id}</title>
          <style>
            body { font-family: 'Tajawal', sans-serif; padding: 20px; }
            h2 { color:#0ea5e9; margin-bottom: 10px; text-align:center; }
            table { width:100%; border-collapse: collapse; margin-top:10px; }
            th, td { border:1px solid #ddd; padding:6px; text-align:center; font-size:13px; }
            th { background:#f3f4f6; }
          </style>
        </head>
        <body>
          <h2>ÿµŸäÿØŸÑŸäÿ© ÿßŸÑŸÖÿπŸÑŸÖ</h2>
          <p>ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑÿ¥ŸÅÿ™: <strong>${shift.id}</strong></p>
          <p>ÿßŸÑŸÉÿßÿ¥Ÿäÿ±: ${shift.cashier}</p>
          <p>ÿ®ÿØÿßŸäÿ© ÿßŸÑÿ¥ŸÅÿ™: ${new Date(shift.openedAt).toLocaleString("ar-EG")}</p>
          <p>ŸÜŸáÿßŸäÿ© ÿßŸÑÿ¥ŸÅÿ™: ${
            shift.closedAt
              ? new Date(shift.closedAt).toLocaleString("ar-EG")
              : "‚Äî ŸÑŸÖ ŸäŸèÿ∫ŸÑŸÇ ÿ®ÿπÿØ"
          }</p>
          <h3>ÿßŸÑŸÖŸÑÿÆŸëÿµ ÿßŸÑŸÖÿßŸÑŸä</h3>
          <table>
            <tbody>
              <tr><th>ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™</th><td>${formatCurrency(
                t.totalSales
              )}</td></tr>
              <tr><th>ÿπÿØÿØ ÿßŸÑŸÅŸàÿßÿ™Ÿäÿ±</th><td>${
                t.invoicesCount || 0
              }</td></tr>
              <tr><th>ŸÜŸÇÿØŸãÿß</th><td>${formatCurrency(
                t.totalCash
              )}</td></tr>
              <tr><th>ÿ®ÿ∑ÿßŸÇÿ©</th><td>${formatCurrency(
                t.totalCard
              )}</td></tr>
              <tr><th>ŸÖÿ≠ŸÅÿ∏ÿ©</th><td>${formatCurrency(
                t.totalWallet
              )}</td></tr>
            </tbody>
          </table>
          <script>
            window.onload = () => {
              setTimeout(() => {
                window.print();
                setTimeout(() => window.close(), 800);
              }, 300);
            };
          </script>
        </body>
      </html>
    `;
    const w = window.open("", "_blank", "width=900,height=900");
    w.document.write(html);
    w.document.close();
  };

  return (
    <Layout user={user} title="ÿßŸÑÿ¥ŸëŸêŸÅÿ™ÿßÿ™">
      <div dir="rtl" className="space-y-6">
        <h1 className="text-xl font-bold text-gray-800">
          ‚è±Ô∏è ÿ•ÿØÿßÿ±ÿ© ÿßŸÑÿ¥ŸëŸêŸÅÿ™ÿßÿ™
        </h1>

        {/* ÿ≠ÿßŸÑÿ© ÿßŸÑÿ¥ŸÅÿ™ ÿßŸÑÿ≠ÿßŸÑŸä */}
        <div className="p-4 space-y-3 bg-white border rounded-lg shadow-sm">
          {currentShift ? (
            <>
              <p className="text-sm text-emerald-700">
                ÿ¥ŸÅÿ™ ŸÖŸÅÿ™Ÿàÿ≠ ÿ≠ÿßŸÑŸäŸãÿß ‚Äî ÿ±ŸÇŸÖ{" "}
                <span className="font-semibold">
                  {currentShift.id}
                </span>{" "}
                ‚Äî ÿßŸÑŸÉÿßÿ¥Ÿäÿ±:{" "}
                <span className="font-semibold">
                  {currentShift.cashier}
                </span>
              </p>
              <p className="text-xs text-gray-600">
                ÿ®ÿØÿßŸäÿ© ÿßŸÑÿ¥ŸÅÿ™:{" "}
                <SafeDate value={currentShift.openedAt} />
              </p>

              <div className="grid grid-cols-1 gap-3 mt-3 md:grid-cols-4">
                <InfoBox
                  label="ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™"
                  value={formatCurrency(
                    currentShift.totals?.totalSales || 0
                  )}
                />
                <InfoBox
                  label="ÿπÿØÿØ ÿßŸÑŸÅŸàÿßÿ™Ÿäÿ±"
                  value={currentShift.totals?.invoicesCount || 0}
                />
                <InfoBox
                  label="ŸÜŸÇÿØŸãÿß"
                  value={formatCurrency(
                    currentShift.totals?.totalCash || 0
                  )}
                />
                <InfoBox
                  label="ÿ®ÿ∑ÿßŸÇÿ© + ŸÖÿ≠ŸÅÿ∏ÿ©"
                  value={formatCurrency(
                    (currentShift.totals?.totalCard || 0) +
                      (currentShift.totals?.totalWallet || 0)
                  )}
                />
              </div>

              <div className="flex flex-col gap-3 mt-4 md:flex-row">
                <input
                  type="text"
                  className="flex-1 p-2 text-sm border rounded"
                  placeholder="ŸÖŸÑÿßÿ≠ÿ∏ÿ© ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑÿ¥ŸÅÿ™ (ÿßÿÆÿ™Ÿäÿßÿ±Ÿä)"
                  value={closingNote}
                  onChange={(e) => setClosingNote(e.target.value)}
                />
                <button
                  onClick={handleCloseShift}
                  className="px-4 py-2 text-sm font-semibold text-white bg-red-600 rounded hover:bg-red-700"
                >
                  ‚õî ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑÿ¥ŸÅÿ™
                </button>
              </div>
            </>
          ) : (
            <>
              <p className="text-sm text-red-600">
                ŸÑÿß ŸäŸàÿ¨ÿØ ÿ¥ŸÅÿ™ ŸÖŸÅÿ™Ÿàÿ≠ ÿ≠ÿßŸÑŸäŸãÿß.
              </p>
              <div className="flex flex-col gap-3 mt-2 md:flex-row">
                <input
                  type="text"
                  className="flex-1 p-2 text-sm border rounded"
                  placeholder="ŸÖŸÑÿßÿ≠ÿ∏ÿ© ÿ®ÿØÿßŸäÿ© ÿßŸÑÿ¥ŸÅÿ™ (ÿßÿÆÿ™Ÿäÿßÿ±Ÿä)"
                  value={openingNote}
                  onChange={(e) => setOpeningNote(e.target.value)}
                />
                <button
                  onClick={handleOpenShift}
                  className="px-4 py-2 text-sm font-semibold text-white rounded bg-emerald-600 hover:bg-emerald-700"
                >
                  ‚úÖ ŸÅÿ™ÿ≠ ÿ¥ŸÅÿ™ ÿ¨ÿØŸäÿØ
                </button>
              </div>
            </>
          )}
        </div>

        {/* ÿ¨ÿØŸàŸÑ ÿßŸÑÿ¥ŸÅÿ™ÿßÿ™ */}
        <div className="p-4 overflow-x-auto bg-white border rounded-lg shadow-sm">
          <h2 className="mb-3 text-lg font-semibold text-gray-800">
            ÿ≥ÿ¨ŸÑ ÿßŸÑÿ¥ŸÅÿ™ÿßÿ™
          </h2>
          <table className="w-full text-sm text-right min-w-[880px]">
            <thead className="text-gray-600 bg-gray-50">
              <tr>
                <th className="px-3 py-2">#</th>
                <th className="px-3 py-2">ÿ±ŸÇŸÖ ÿßŸÑÿ¥ŸÅÿ™</th>
                <th className="px-3 py-2">ÿßŸÑŸÉÿßÿ¥Ÿäÿ±</th>
                <th className="px-3 py-2">ÿßŸÑÿ®ÿØÿßŸäÿ©</th>
                <th className="px-3 py-2">ÿßŸÑŸÜŸáÿßŸäÿ©</th>
                <th className="px-3 py-2">ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™</th>
                <th className="px-3 py-2">ÿπÿØÿØ ÿßŸÑŸÅŸàÿßÿ™Ÿäÿ±</th>
                <th className="px-3 py-2">ÿßŸÑÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
              </tr>
            </thead>
            <tbody>
              {shifts.length ? (
                shifts
                  .slice()
                  .reverse()
                  .map((s, idx) => {
                    const t = s.totals || {};
                    return (
                      <tr
                        key={s.id}
                        className="border-t hover:bg-gray-50"
                      >
                        <td className="px-3 py-2">
                          {idx + 1}
                        </td>
                        <td className="px-3 py-2 font-medium text-sky-700">
                          {s.id}
                        </td>
                        <td className="px-3 py-2">
                          {s.cashier}
                        </td>
                        <td className="px-3 py-2">
                          <SafeDate value={s.openedAt} />
                        </td>
                        <td className="px-3 py-2">
                          {s.closedAt ? (
                            <SafeDate value={s.closedAt} />
                          ) : (
                            <span className="text-xs text-emerald-700">
                              ŸÖŸÅÿ™Ÿàÿ≠
                            </span>
                          )}
                        </td>
                        <td className="px-3 py-2 font-semibold text-emerald-700">
                          {formatCurrency(t.totalSales || 0)}
                        </td>
                        <td className="px-3 py-2">
                          {t.invoicesCount || 0}
                        </td>
                        <td className="px-3 py-2">
                          <button
                            onClick={() => handlePrintShift(s)}
                            className="px-3 py-1 text-xs border rounded border-sky-200 text-sky-700 hover:bg-sky-50"
                          >
                            üñ®Ô∏è ÿ∑ÿ®ÿßÿπÿ© ÿßŸÑÿ™ŸÇÿ±Ÿäÿ±
                          </button>
                        </td>
                      </tr>
                    );
                  })
              ) : (
                <tr>
                  <td
                    colSpan="8"
                    className="py-6 text-center text-gray-500"
                  >
                    ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ¥ŸÅÿ™ÿßÿ™ ŸÖÿ≥ÿ¨ŸÑÿ©.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
    </Layout>
  );
}

function InfoBox({ label, value }) {
  return (
    <div className="p-3 text-center border rounded bg-gray-50">
      <p className="text-xs text-gray-500">{label}</p>
      <p className="mt-1 text-sm font-semibold text-gray-800">
        {value}
      </p>
    </div>
  );
}













